@import url(https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@1,900&family=Roboto:wght@300;400;500;700&display=swap);
.projectItem, .projectItem .content, .projectItem .overlay {
  border-radius: 16px;
}

.projectItem {
  --icon-size: 40px;
  padding-bottom: 100%;
  border-width: 2px;
}

.projectItem .content svg {
  width: var(--icon-size);
  height: var(--icon-size);
  transition: all 0.2s;
}

.projectItem .content span {
  width: 100%;
  font-size: var(--icon-size);
  line-height: var(--icon-size);
  text-align: center;
  transition: all 0.2s;
}

.projectItem:hover {
  -webkit-transform: translateY(-6px);
  transform: translateY(-6px);
  border: 0px;
}

.projectItem .overlay {
  height: 50px;
  visibility: hidden;
}

.projectItem .overlay span {
  font-size: 0px !important;
}

.projectItem:hover .overlay {
  visibility: visible;
  visibility: initial;
  height: 100% !important;
  background-color: var(--active-color) !important;
  font-size: medium !important;
  font-size: initial !important;
}

.projectItem:hover .overlay span {
  font-size: 1rem !important;
  line-height: 1.5rem !important;
}
section#home > header h1 {
  font: 900 2.4rem 'Merriweather', serif;
}
.linkHover svg {
  transition: all 0.2s;
}

.linkHover:hover svg {
  color: var(--primary-purple) !important;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.button {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
  padding: 12px;
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.2s;
}

.button.primary {
  background-color: var(--primary-purple);
}

.button:hover {
  -webkit-filter: brightness(0.8);
  filter: brightness(0.8);
}
@-webkit-keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

section#create header a svg {
  transition: all 0.2s;
}

section#create header a:hover svg {
  color: #a143b4 !important;
  -webkit-transform: scale(1.2);
  transform: scale(1.2);
}
.TaskTimeline .monthBox {
  border-left-width: 2px;
}

.TaskTimeline .monthBox .dayBox:first-child {
  border-left-width: 0px;
}

.TaskTimeline .monthBox .dayBox {
  border-left-width: 1px;
  border-right-width: 1px;
}
section#project aside.taskList table tbody tr:last-child {
  border-radius: 2px 0px 0px 2px;
}
.select-container.active .select-button {
  border-color: var(--primary-purple);
  outline: none;
  border-bottom-left-radius: 0px;
  border-bottom-right-radius: 0px;
  color: var(--primary-purple);
  transition: all 0.1s;
}

.select-container .select-button svg {
  transition: all 0.1s;
}

.select-container.active .select-button svg {
  -webkit-transform: rotateX(180deg);
  transform: rotateX(180deg);
}

.select-container.active .select-options {
  border-top-left-radius: 0px;
  border-top-right-radius: 0px;
  border-color: var(--primary-purple);
}

.select-container .select-option:hover {
  background-color: var(--primary-purple);
}

